import { CognitoAccessToken, CognitoIdToken, CognitoRefreshToken, CognitoUser, CognitoUserPool, CognitoUserSession } from "amazon-cognito-identity-js";
var poolData = {
    UserPoolId: TDR_USER_POOL_ID,
    ClientId: UPLOAD_APP_CLIENT_ID
};
export function getUserPool() {
    return new CognitoUserPool(poolData);
}
export function getCurrentUser() {
    return getUserPool().getCurrentUser();
}
export function getSession(currentUser) {
    return new Promise(function (resolve, reject) {
        currentUser.getSession(function (err, session) {
            if (err) {
                reject(err);
            }
            else {
                resolve(session);
            }
        });
    });
}
export function authenticateUser(authenticationCode) {
    var userPool = getUserPool();
    var tokenEndpoint = TDR_AUTH_URL + "/oauth2/token";
    return fetch(tokenEndpoint, {
        method: 'POST',
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: "grant_type=authorization_code&scope=profile&client_id=" + UPLOAD_APP_CLIENT_ID + "&redirect_uri=" + TDR_BASE_URL + "/upload&code=" + authenticationCode
    }).then(function (res) {
        return res.json();
    }).then(function (json) {
        var idToken = new CognitoIdToken({ IdToken: json.id_token });
        var decoded = idToken.decodePayload();
        // If we decide to use this auth mechanism in production, we should verify that this username matches
        // the user that logged into the app.
        var userName = decoded["cognito:username"];
        var session = new CognitoUserSession({
            IdToken: new CognitoIdToken({ IdToken: json.id_token }),
            RefreshToken: new CognitoRefreshToken({ RefreshToken: json.refresh_token }),
            AccessToken: new CognitoAccessToken({ AccessToken: json.access_token })
        });
        var user = new CognitoUser({
            Username: userName,
            Pool: userPool
        });
        user.setSignInUserSession(session);
    });
}
