import * as React from "react";
import * as ReactDOM from "react-dom";
import { FileUpload } from "./components/FileUpload";
import { updateFileStatuses } from "./fileStatus";
window.onload = function () {
    var fileUploadContainer = document.querySelector('#file_upload_container');
    if (fileUploadContainer) {
        ReactDOM.render(React.createElement(FileUpload, null), fileUploadContainer);
    }
    var fileStatusContainer = document.querySelector(".file-status");
    if (fileStatusContainer) {
        updateFileStatuses();
    }
};
